<template>
     <el-form :model="searchForm" ref="searchForm"  label-width="80px" >
            <el-row>
                  <el-col :span="4">
                    <el-form-item label="仓库" prop="warehouseUuid">
                      <el-select v-model="searchForm.warehouseUuid" filterable>
                          <el-option
                            v-for="(item, index) in warehouseList"
                            :key="index"
                            :label="item.warehouseName"
                            :value="item.warehouseUuid"
                          />
                      </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                  <el-form-item label="开始日期" label-width="100px" prop="beginDate">
                    <el-date-picker
                      class="w100p"
                      v-model="searchForm.beginDate"
                      type="date"
                      align="right"
                      clearable
                      unlink-panels
                      placeholder="请选择开始日期"
                      format="yyyy 年 MM 月 dd 日"
                      value-format="yyyy-MM-dd"
                      size="mini"
                    >
                    </el-date-picker>
                  </el-form-item>
                </el-col>
              <el-col :span="6">
                  <el-form-item label="截止日期" label-width="100px" prop="endDate">
                    <el-date-picker
                      class="w100p"
                      v-model="searchForm.endDate"
                      type="date"
                      align="right"
                      clearable
                      unlink-panels
                      placeholder="请选择截止日期"
                      format="yyyy 年 MM 月 dd 日"
                      value-format="yyyy-MM-dd"
                      size="mini"
                    >
                    </el-date-picker>
                  </el-form-item>
                </el-col>
              <el-col :span="4">
                <el-form-item label="状态" prop="inventroyResult">
                  <el-select filterable clearable v-model="searchForm.inventroyResult" class="w100p">
                    <el-option class="radio" value="1" label="未开始"></el-option>
                    <el-option class="radio" value="2" label="进行中"></el-option>
                    <el-option class="radio" value="3" label="已完成"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
                <el-col :span="3" class="flex-right">
                    <el-button type="primary" @click.native="search">查询</el-button>
                    <el-button @click="resetForm('searchForm')">重置</el-button>
                </el-col>
            </el-row>
          </el-form>
</template>

<script>
  import { setSession, getSession } from '@/utils/session'
  import moment from 'moment'

  export default {
  name: 'StockCheckSearch',
  data () {
    return {
      // 检索界面数据
      orgList: [],
      searchForm: {
        warehouseUuid: '',
        checkDate: '',
        inventroyResult: '',
        beginDate:'',
        endDate:''
      }
    }
  },
  props: {
    warehouseList: {
      type: Array,
      required: true
    }
  },
    activated () {
      this.searchForm= {
        warehouseUuid: '',
        checkDate: '',
        taskState: ''
      }
      this.searchForm.warehouseUuid = getSession('warehouseUuid')
      this.search()
    },
  mounted () {
    this.searchForm.warehouseUuid = getSession('warehouseUuid')
    this.$emit('search', Object.assign({}, this.searchForm))
  },
  methods: {
    // 检索
    search: function () {
      if (this.searchForm.endDate && this.searchForm.beginDate && ( moment(this.searchForm.endDate).isBefore(this.searchForm.beginDate) || moment(this.searchForm.endDate).isSame(this.searchForm.beginDate))) {
        this.$message({
          message: '截止日期不能早于开始日期，请重新选择！',
          type: 'warning'
        })
        this.searchForm.endDate = null
      } else {
        this.$emit('search', Object.assign({}, this.searchForm))
      }
    },
    // 重置
    resetForm (formName) {
      this.$refs[formName].resetFields()
      this.searchForm.warehouseUuid = getSession('warehouseUuid')
      this.$emit('rest', Object.assign({}, this.searchForm))
    }
  }
}
</script>
<style lang="scss" scoped>
.el-form-item{
   margin-bottom:10px;
}
</style>



